* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  background: #f7f7f7;
  color: #242424;
  font-size: 1rem;
}

.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
}

.choice-column {
  width: 300px;
  padding: 48px 36px;
  background-color: #fff;
  box-shadow: 0px 1px 1px #ccc;
}

.card__score {
  position: absolute;
  right: 20px;
  color: #b2b2b2;
}

.suggestion-container {
  padding: 48px 48px 0 48px;
  display: flex;
  flex-direction: column;
}

.card {
  padding: 16px 20px;
  min-width: 120px;
  max-width: 680px;
  position: relative;
  margin-bottom: 40px;
  background-color: #fff;
  box-shadow: 0px 1px 1px #ccc;
}

h3 {
  font-size: 1rem;
  color: #999;
  font-weight: 500;
  padding-bottom: 12px;
}

h4 {
  font-weight: 500;
}

p {
  font-weight: 300;
  padding: 8px 0;
}

.list {
  margin-bottom: 44px;
  padding: 0;
  list-style: none;
}

.input-toggle input {
  padding: 0;
  margin: 0;
  position: absolute;
  left: 0;
  /*opacity: 0;*/
  height: 22px;
  width: 22px;
}

.input-toggle label {
  cursor: pointer;
  color: #474445;
  display: block;
  padding: 2px 0 2px 28px;
  line-height: 22px;
  font-size: 16px;
  font-weight: 400;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: relative;
  -webkit-tap-highlight-color: transparent;
}

.input-toggle label.disabled {
  color: #ccc;
}

.input-toggle {
  padding: 1px 0;
  position: relative;
}
